@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.tile {
  width: var(--size-tile);
  height: var(--size-tile);
  background-color: var(--color-white);
  margin: 0.5rem;
  border-radius: 50%;
  box-shadow: 2px 2px 5px var(--color-dark-gray);
}

.tile:hover {
  background-color: var(--color-light-gray);
}

.tile--red, .tile--red:hover {
  background-color: var(--color-light-red);
  border: 3px solid var(--color-red);
}

.tile--yellow, .tile--yellow:hover {
  background-color: var(--color-light-yellow);
  border: 3px solid var(--color-yellow);
}

.tile--winner {
  border-color: var(--color-black);
}

.tile--winner::after {
  content: 'Winner';
  font-weight: bold;
  display: inline-block;
}
.board {
  width: auto;
  background-color: var(--color-black);
  padding: 1rem;
  border-radius: 1rem;
  position: relative;
  box-shadow: 2px 2px 10px var(--color-dark-gray);
}

.board .board--gameover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: var(--color-black);
  background: var(--color-light-gray-overlay);
  border-radius: inherit;
  padding-top: 10rem;
  text-align: center;
  z-index: 10;
}

.board-header {
  position: relative;
}

.board-header .board-header__title {
  padding: 0;
  margin: 0;
}

.board-header .board-header__highlight {
  font-weight: bold;
  text-transform: uppercase;
}

.board-header .board-header__reset {
  position: absolute;
  top: 0;
  right: 0;
}
.board-history {
  max-height: 550px;
  overflow-y: scroll;
}

.board-history .board-history__item {
  width: 100%;
  margin-top: 1rem;
  text-align: left;
  text-transform: uppercase;
}

.board-sidebar {
  width: 100%;
  height: 100%;
  padding: 2rem;
  padding-top: 0rem;
}

.app .app__header {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.app .app__footer {
  margin-top: 3rem;
}

.app .app__about {
  float: right;
}
:root {
  --color-yellow: #fae912;
  --color-light-yellow: #f8f879;

  --color-red: #ef1d2b;
  --color-light-red: #f2725d;

  --color-white: rgb(241, 240, 240);
  --color-black: #343a40;

  --color-dark-gray: rgb(117, 128, 141);
  --color-gray: #e7e6e6;
  --color-light-gray: whitesmoke;
  --color-light-gray-overlay: rgba(245, 245, 245, 0.6);

  --size-tile: 5.5rem;
}

body {
  margin: 0;
  font-family: 'Montserrat', 'Courier New', 'Helvetica Neue', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: lightslategrey;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: #f2725d !important;
  color: var(--color-light-red) !important;
  text-decoration: none !important;
}

a:hover {
  color: #ef1d2b !important;
  color: var(--color-red) !important;
}

.badge {
  margin-right: 0.4rem;
}
